@font-face {
  font-family: 'Menlo';
  src: url('/public/fonts/menlo-bold.woff2') format('woff2'),
       url('/public/fonts/menlo-bold.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}


.home {
    max-width: 600px;
    margin: 40px auto;
    padding: 30px;
    text-align: center;
    font-family: Menlo, monospace;
    background: #f6f5f8;
    border: 4px solid #000;
    border-radius: 6px;
    box-shadow: 6px 6px 0 #404040;
  }
  
  .home-title {
    font-size: 40px;
    text-align: center;
    margin: 0 auto 20px;
    margin-top: 10px;
    background: #2dd1a3;
    border: 4px solid #000;
    padding: 12px;
    box-shadow: 6px 6px 0 #404040;
    border-radius: 6px;
    transition: transform 0.2s ease;
    display: flex;
    justify-content: center;
    width: fit-content;
  }
  
  .home-section {
    margin-bottom: 30px;
    padding: 25px;
    border: 4px solid #000;
    background: #fafafa;
    box-shadow: 5px 5px 0 #404040;
    border-radius: 8px;
    transition: transform 0.2s ease;
  }
  
  .home-section h2 {
    font-size: 26px;
    margin-bottom: 12px;
    font-weight: 700;
    color: #000;
  }
  
  .home-section p {
    font-size: 16px;
    line-height: 1.6;
    margin-top: 5px;
    color: #010101;
  }
  
  .home-buttons {
    display: flex;
    flex-direction: column;
    gap: 12px;
    margin-top: 15px;
  }
  
  .home-button {
    display: block;
    text-align: center;
    padding: 14px;
    background: #a47beb;
    border: 4px solid #000;
    box-shadow: 5px 5px 0 #404040;
    border-radius: 8px;
    color: #000;
    font-weight: 700;
    font-size: 18px;
    text-decoration: none;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
  }
  
  .home-button:hover {
    transform: translate(-2px, -2px);
    box-shadow: 7px 7px 0 #404040;
    background: #b89bf7;
  }
  
@media screen and (max-width: 768px) {
  body, html {
    font-size: 16px; /* Slightly smaller base font size for mobile */
    letter-spacing: 0.8px;
  }
  
  .home {
    margin: 20px 10px;
    padding: 20px;
    max-width: 100%;
    box-shadow: 4px 4px 0 #404040;
  }

  .home-title {
    font-size: 32px;
    padding: 10px;
    box-shadow: 4px 4px 0 #404040;
  }

  .home-section {
    padding: 20px;
    margin-bottom: 20px;
    box-shadow: 4px 4px 0 #404040;
  }

  .home-section h2 {
    font-size: 22px;
  }

  .home-section p {
    font-size: 14px;
  }

  .home-button {
    font-size: 16px;
    padding: 12px;
    box-shadow: 4px 4px 0 #404040;
  }

  .home-button:hover {
    transform: translate(-1px, -1px);
    box-shadow: 5px 5px 0 #404040;
  }
}